const globalVariables = {
  view: {
    start: 'StartView',
    main: 'MainView',
    work: 'WorkView',
    test: 'TestView',
    page404: 'Page404',
  },
  commonView: {
    roots: {
      main: 'MainRoot',
      shop: 'ShopRoot',
      leaderboard: 'LeaderboardRoot',
    },
    panels: {
      main: 'Main',
      questionDetails: 'QuestionDetails',
      shop: 'Shop',
      shopQuestion: 'ShopQuestion',
      leaderboard: 'Leaderboard',
    },
  },
  authToken: 'authToken',
  friendsAccessToken: 'friendsAccessToken',
  notificationAllow: 'notificationAllow',
  serverURL: 'https://mindopoly.tech',
  quizResult: 'quizResult',
  quizQuestions: 'quizQuestions',
  tooltips: 'tooltips',
  translateEnToRu(category) {
    switch (category) {
      case 'All':
        return '';
      case 'Math':
        return '';
      case 'Russian':
        return ' ';
      case 'Literature':
        return '';
      case 'Physics':
        return '';
      case 'Chemistry':
        return '';
      case 'Astronomy':
        return '';
      case 'Biology':
        return '';
      case 'History':
        return '';
      case 'Art':
        return '';
      case 'Sport':
        return '';
      case 'Geography':
        return '';
      case 'Other':
        return '';
      default:
        return category;
    }
  },
  categoryColor(category) {
    switch (category) {
      case 'Math':
        return '#E64646';
      case 'Russian':
        return '#857250';
      case 'Literature':
        return '#FFA000';
      case 'Physics':
        return '#3F8AE0';
      case 'Chemistry':
        return '#792EC0';
      case 'Astronomy':
        return '#6D7885';
      case 'Biology':
        return '#4CD964';
      case 'History':
        return '#63B9BA';
      case 'Art':
        return '#E5188F';
      case 'Sport':
        return '#28436E';
      case 'Other':
        return '#909499';
      case 'Geography':
        return '#4BB34B';
      default:
        return '';
    }
  },
  mainViewModal(name) {
    switch (name) {
      case 'Tax': return 'TaxDetailsModalPage';
      case 'GPtoday': return 'GPDetailsModalPage';
      case 'CoinsToday': return 'CoinsDetailsModalPage';
      case 'Mindopolist': return 'MindopolistDetailsModalPage';
      case 'Pioneer': return 'PioneerModalPage';
      default: return null;
    }
  },
};

export default globalVariables;
